form .txt {
    display: inline-block;
    padding: 8px 9px;
    padding-right: 30px;
    width: 300px;
    font-family: 'Oxygen', sans-serif;
    font-size: 16px;
    font-weight: normal;
    color: #898989;
    background-color: #f0f0f0;
    /*background-image: url('./green-tick-png-green-tick-png-file-570.png');*/
    /*background-position: 110% center;*/
    /*background-repeat: no-repeat;*/
    /*background-size: 10%;*/
    border: 1px solid #ccc;
    text-shadow: 0 1px 0 rgba(255,255,255,0.75);
    box-sizing: content-box;
    border-radius: 3px;
    box-shadow: 0 1px 2px rgba(25, 25, 25, 0.25) inset, -1px 1px #fff;
    transition: all 0.3s linear;
}

form .txt:focus{
    width: 305px;
    color: #545454;
    background-color: #fff;
    background-position: 110% center;
    background-repeat: no-repeat;
    border-color: #059;
    box-shadow: 0 1px 4px -1px #a8a8a8 inset, 0 1px rgba(255, 255, 255, 0.6), 0 0 11px rgba(70, 100, 200, 0.7);
}

form .txt:valid {
    background-color: #deecda;
    background-position: 98% center;
    background-repeat: no-repeat;
    color: #7d996e;
    border: 1px solid #95bc7d;
}

form .txt:focus:valid{
    box-shadow: 0 1px 4px -1px #a8a8a8 inset, 0 1px rgba(255, 255, 255, 0.6), 0 0 11px rgba(120, 200, 70, 0.7);
