#submitbtn {
    border-radius: 30px;
    height: 50px;
    width: 175px;
    padding: 0;
    cursor: pointer;
    font-family: 'Oxygen', Arial, sans-serif;
    font-size: 14px;
    color: #0a528f;
    text-shadow: 1px 1px 0 rgba(255,255,255,0.65);
    border-width: 1px;
    border-style: solid;
    border-color: #317bd6 #3784e3 #2d74d5 #3774e3;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    background-color: #4581e5;
    box-shadow: 1px 1px 3px rgba(0,0,0,0.4), 0 1px 0 rgba(255, 255, 255, 0.5) inset;
    transition: all 0.3s linear;
}
#submitbtn:hover, #submitbtn:focus {
    box-shadow: 0 0 15px rgba(70, 100, 200, 0.9);
}

#submitbtn:active {
    box-shadow: 0 0 15px rgba(70, 100, 200, 0.9), 0 1px 3px rgba(0,0,0,0.4) inset !important;
    -webkit-box-shadow: 0 0 15px rgba(70, 100, 200, 0.9), 0 1px 3px rgba(0,0,0,0.4) inset !important;
    -moz-box-shadow: 0 0 15px rgba(70, 100, 200, 0.9), 0 1px 3px rgba(0,0,0,0.4) inset;
}


.MainAuth input:after {
