.chat-bubble {
    padding: 10px 14px;
    background: #ccc;
    margin: 10px 30px;
    border-radius: 9px;
    position: relative;
    -webkit-animation: fadeIn 1s ease-in;
            animation: fadeIn 1s ease-in;
    margin-right: 49px;
    margin-left: 0;
    word-wrap: break-word; /* All browsers since IE 5.5+ */
    /*overflow-wrap: break-word;*/

}

.chat-bubble-img-left, .chat-bubble-img-right {
    background: #fff !important;

}

.chat-bubble-img-right img, .chat-bubble-img-left img {
    height: 250px;
    width: 335px;
}

.chat-bubble-img-left img {
    margin-left: -14px;
}

.chat-bubble-img-right img {
    margin-left: -208px;
}

.chat-bubble-img-left:after {
    border-right-color: #fff !important;
}

.chat-bubble-img-right:after {
    border-left-color: #fff !important;
}
.chat-bubble span{
    font-family: Palatino Linotype, sans-serif
}
.chat-bubble:after {
    content: '';
    position: absolute;
    top: 50%;
    width: 0;
    height: 0;
    border: 20px solid transparent;
    border-bottom: 0;
    margin-top: -10px;
}

.chat-bubble--left:after {
    left: 0;
    border-right-color: #ccc;
    border-left: 0;
    margin-left: -20px;
}

.chat-bubble.chat-bubble--left {
    margin-left: 33px;
    margin-right: 14px;
}

.chat-bubble--right:after {
    right: 0;
    border-left-color: #74b9ff;
    border-right: 0;
    margin-right: -20px;
}

